import { Component, OnInit } from '@angular/core';
import { Material, User } from "process-smart-ng6-lib";
import { InventoryManagementService, DocumentConversionService, AuthenticationService } from "process-smart-ng6-lib";
import { SharedService } from "src/app/_services/shared.service";
import { CustomDownloadServiceService } from "src/app/_services/custom-download-service.service";
import { saveAs } from 'file-saver';
import { ActivatedRoute } from "@angular/router";

@Component({
  selector: 'app-view-materials',
  templateUrl: './view-materials.component.html',
  styleUrls: ['./view-materials.component.css']
})
export class ViewMaterialsComponent implements OnInit {
    itemsList: Array<Material>;
    partialItemsList: Array<Material>;
    usersList: Array<User>;
    deptId: string;
    is_downloading_list: boolean;
    constructor(private ims: InventoryManagementService, private adms: AuthenticationService,
          private dcs: DocumentConversionService, private ssv: SharedService,
          private route: ActivatedRoute, private cds: CustomDownloadServiceService) { }
    
    ngOnInit() {
        this.route.params.subscribe(params => {
            this.deptId = params['id'];
            if(this.deptId){
                this.populateItems();
            }
        });
        this.is_downloading_list = false;
    }
    
    populateItems() {
    this.ims.getMaterials(this.deptId)
      .subscribe(itemsList => {
          this.itemsList = itemsList;
          this.partialItemsList = itemsList;
      });
    }
    
    downloadAll(){
        this.is_downloading_list = true;
        this.cds.downloadMaterialsListXls(this.deptId)
        .subscribe(
            res => {
                saveAs( res, 'InventoryList.xls', 
                    { type: 'application/vnd.ms-excel' });
                this.is_downloading_list = false;
            },
            error => {
                // Display error message
                console.log(error);
                this.ssv.showError('Error: Unable to download list!');
                this.is_downloading_list = false;
            }
        );
    }
    
    searchItem(input: string) {
      if (this.partialItemsList && this.itemsList) {
        this.partialItemsList = this.itemsList.filter(x => 
            (x.description.toLowerCase().includes(input.toLowerCase())));
      }
    }
}
