import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-app-updates',
  templateUrl: './app-updates.component.html',
  styleUrls: ['./app-updates.component.css']
})
export class AppUpdatesComponent implements OnInit {

    version: string;
    release_date: string;
    downloadLink: string;
    
  constructor() { }

  ngOnInit() {
      const roleId = JSON.parse(localStorage.getItem('currentUser')).user.role_id;
      switch(roleId){
      //Maintenance App(Tom and PMs)
      case 2:
      case 3: this.downloadLink = 'https://s3-us-west-1.amazonaws.com/process-smart-attachments/android_apk/1/NisshoMaintenance.apk';
                this.version = 'v.3.2';
                this.release_date = '07/10/2019';  
      break;
      //Irrigation App
      case 4:
      case 6: this.downloadLink = 'https://s3-us-west-1.amazonaws.com/process-smart-attachments/android_apk/1/NisshoIrrigation.apk';
                this.version = 'v.3.0';  
                this.release_date = '05/13/2019';  
      break;
      //Janitorial App
      case 13: this.downloadLink = 'https://s3-us-west-1.amazonaws.com/process-smart-attachments/android_apk/1/NisshoJanitorial.apk';
                 this.version = 'v.2.0';  
                 this.release_date = '05/13/2019';  
      break;
      default: this.downloadLink = '';
      }
      
  }

}
